* {
    box-sizing: border-box;
}

html,
body,
button,
input {
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    color: #333;
    line-height: 26px;
    outline: none;
}

html {
    height: 100%;
}

body {
    margin: 0;
    height: 100%;
    user-select: none;
}

button {
    cursor: pointer;
}